/* eslint-disable @typescript-eslint/no-explicit-any */
import * as React from 'react';
import {
  Button,
  EmptyState,
//   EmptyStateActions,
  EmptyStateBody,
  EmptyStateFooter,
  EmptyStateHeader,
  EmptyStateIcon,
  EmptyStateVariant,
  PageSection,
  Text,
  TextContent,
//   TextVariants,
  Title,
} from '@patternfly/react-core';
import { useEffect } from 'react';
import { Services } from '@app/apis/services';
import { AppLayoutContext } from '@app/AppLayout';
import { CubesIcon } from '@patternfly/react-icons';

const ConnectorPage: React.FunctionComponent = () => {
  const [loading, setLoading] = React.useState<boolean>(false);
  const [connectors, setConnectors] = React.useState<any[]>([]);

  const appLayoutContext = React.useContext(AppLayoutContext);
  const clusterUrl = appLayoutContext.cluster;

  useEffect(() => {
    if (clusterUrl) {
      setLoading(true);
      const connectorService = Services.getConnectorService();
      connectorService
        .getConnectorsStatus(clusterUrl)

        .then((cConnector) => {
          setLoading(false);
          setConnectors(cConnector);
          console.log(cConnector);
        })
        .catch((err: React.SetStateAction<Error>) => {
          console.log(err);
        });
    }
  }, [clusterUrl]);

  return (
    <PageSection>
      {loading ? (
        <p>Loading...</p>
      ) : connectors.length < 1 ? (
        <EmptyState variant={EmptyStateVariant.lg}>
          <EmptyStateHeader
            titleText="No running connector"
            icon={<EmptyStateIcon icon={CubesIcon} />}
            headingLevel="h1"
          />
          <EmptyStateBody>
            <TextContent>
              <Text component="p">
                You can create a new connector by clicking on the &quot;Create connector&quot; button.
              </Text>
              {/* <Text component={TextVariants.small}>
                This text has overridden a css component variable to demonstrate how to apply customizations using
                PatternFly&apos;s global variable API.
              </Text> */}
            </TextContent>
          </EmptyStateBody>
          <EmptyStateFooter>
            <Button variant="primary">Create connector</Button>
            {/* <EmptyStateActions>
              <Button variant="link">Multiple</Button>
              <Button variant="link">Action Buttons</Button>
              <Button variant="link">Can</Button>
              <Button variant="link">Go here</Button>
              <Button variant="link">In the secondary</Button>
              <Button variant="link">Action area</Button>
            </EmptyStateActions> */}
          </EmptyStateFooter>
        </EmptyState>
      ) : (
        <Title headingLevel="h1" size="lg">
          Dashboard Page Title!
        </Title>
      )}
    </PageSection>
  );
};

export { ConnectorPage };
